
































































import Badges from "../components/Badges.vue";

import Vue from "vue";
import { Component } from "vue-property-decorator";
@Component({
  components: {
    Badges,
  },
})
export default class Security extends Vue {
  $store: any;

  refresh() {
    this.$store.dispatch("fetchUsers");
  }

  get users() {
    return this.$store.state.users;
  }

  get subForums() {
    return this.$store.state.subForums;
  }

  get currentUser() {
    return this.$store.state.currentUser;
  }

  deleteUser(id: string) {
    this.$store.dispatch("deleteUser", id);
    this.$store.dispatch("fetchUsers");
  }

  created() {
    this.$store.dispatch("fetchUsers");
    this.$store.dispatch("fetchSubForums");
  }

  async addRole(userId: number, subforumId: number) {
    const body = {
      userId,
      subforumId,
    };
    await fetch("/api/roles", {
      method: "POST",
      headers: {
        "Content-Type": "Application/json",
      },
      body: JSON.stringify(body),
    })
      .then((response) => {
        if (response.status === 201) {
          return response.json();
        }
      })
      .catch((error) => {
        console.error(error);
      });
  }

  async deleteRole(userId: number, subforumId: number) {
    await fetch("/api/roles/" + userId, {
      method: "DELETE",
      headers: {
        "Subforum-Id": `${subforumId}`,
      },
    });
  }
}
